// Core
import React, { Component } from 'react';

// Components
import SectionHeading from './SectionHeading';
import Eduspace from './Eduspace';

// Data
import education from '../theme/assets/data/education.json';

export default class Education extends Component {
    render () {
        const educationJSX = education.map(
            ({ id, degree, eduspace, period }) => (
                <Eduspace
                    degree = { degree }
                    eduspace = { eduspace }
                    key = { id }
                    period = { period }
                />
            ),
        );

        return (
            <div className = 'education'>
                <SectionHeading number = '04.' title = 'Образование' />
                {educationJSX}
                <span className = 'copyright'>2018 © Илон Маск</span>
            </div>
        );
    }
}
