// Core
import React, { Component } from 'react';
import cx from 'classnames';

export default class Skill extends Component {
    render () {
        const { title, points } = this.props;

        const pointsJSX = [...Array(10).keys()].map((element) => {
            const pointStyles = cx({
                inactive: points <= element,
            });

            return <li className = { pointStyles } key = { element } />;
        });

        return (
            <div className = 'skill'>
                <span>{title}</span>
                <ul>{pointsJSX}</ul>
            </div>
        );
    }
}
