// Core
import React, { Component } from 'react';

// Components
import SectionHeading from './SectionHeading';
import Workplace from './Workplace';

// Data
import experience from '../theme/assets/data/experience.json';

export default class Experience extends Component {
    render () {
        const workplaces = experience.map(
            ({ id, company, location, period, position }) => (
                <Workplace
                    company = { company }
                    key = { id }
                    location = { location }
                    period = { period }
                    position = { position }
                />
            ),
        );

        return (
            <section>
                <SectionHeading number = '02.' title = 'Опыт работы' />
                {workplaces}
            </section>
        );
    }
}
