// Core
import React, { Component } from 'react';

// Components
import SectionHeading from './SectionHeading';
import Skill from './Skill';

// Data
import skillset from '../theme/assets/data/skillset.json';

export default class Skillset extends Component {
    render () {
        const skillsetJSX = skillset.map(({ id, points, title }) => (
            <Skill key = { id } points = { points } title = { title } />
        ));

        return (
            <section className = 'skillset'>
                <SectionHeading number = '01.' title = 'Набор скилов' />
                {skillsetJSX}
            </section>
        );
    }
}
